<template>
  <dialog-modal
    ref="dialog"
    :title="title"
    :btnDisabled="btnDisabled"
    @confirm="sendService"
  >
    <v-row>
      <v-col cols="12" class="px-2 py-0 mt-2">
        <label class="label">
          Escolha o tipo de servio:
        </label>
        <select-field
          id="serviceType"
          placeholder="Escolha o tipo de servio"
          :label="null"
          :items="servicesTypes"
          :outlined="true"
          :dense="true"
          :rules="serviceTypeRules"
          :value="service ? service.service_type_id : ''"
          @onChange="setValue"
        />
      </v-col>
      <v-col cols="12" class="px-2 py-0">
        <label class="label">
          Nome do servio:
        </label>
        <input-field
          id="name"
          :label="null" 
          placeholder="Ex. Corte mquina"
          :dense="true"
          :outlined="true"
          :value="service ? service.name : ''"
          :rules="descriptionRules"
          @onKeyUp="setValue"
        />
      </v-col>
      <v-col cols="12" sm="6" class="px-2 py-0">
        <label class="label">
          Durao:
        </label>
        <select-field
          id="duration"
          :label="null"
          placeholder="Durao"
          :dense="true"
          :value="service ? service.duration : ''"
          :items="durationTimes"
          :outlined="true"
          :rules="durationTimeRules"
          @onChange="setValue"
        />
      </v-col>
      <v-col cols="12" sm="6" class="px-2 py-0">
        <label class="label">
          Valor:
        </label>
        <money-field
          :dense="true"
          id="price"
          :label="null"
          placeholder="Valor"
          @onKeyUp="setValue"
          :outlined="true"
          :value="service ? service.price : ''"
        />
      </v-col>
      <v-col cols="12" class="px-2 py-0">
        <label class="label">Descrio:</label>
        <textarea-field
          id="description"
          :label="null"
          :maxLength="100"
          :outlined="true"
          placeholder="Ex: corte com mquina com disfarce na tesoura"
          :value="service ? service.description : ''"
          :hideDetails="true"
          @onKeyUp="setValue"
        />
      </v-col>
       <v-col cols="12" class="px-2 pb-4">
        <checkbox-field
          id="active"
          label="Ativo?"
          :isChecked="active"
          @onChange="setValue"
          />
      </v-col>
    </v-row>
  </dialog-modal>
</template>

<script>
import DialogModal from '@/components/globais/dialogs/default/Default'
import InputField from '@/components/globais/form/input-field/InputField'
import MoneyField from '@/components/globais/form/money-field/MoneyField'
import SelectField from '@/components/globais/form/select-field/SelectField'
import TextareaField from '@/components/globais/form/textarea-field/TextareaField'
import CheckboxField from '@/components/globais/form/CheckboxField'

import BarbershopMixin from '@/mixins/BarbershopMixin'
import DialogMixin from '@/mixins/DialogMixin'
import ServiceMixin from '@/mixins/ServiceMixin'
import TextMixin from '@/mixins/TextMixin'
import TimeMixin from '@/mixins/TimeMixin'

export default {
  name: 'Create',
  components: { DialogModal, CheckboxField, InputField, MoneyField, SelectField, TextareaField },
  mixins: [ BarbershopMixin, DialogMixin, ServiceMixin, TextMixin, TimeMixin ],
  props: {
    title: {
      default: ''
    }
  },
  data: () => ({
    description: null,
    name: null,
    active: true,
    duration: null,
    serviceType: null,
    price: 0.00,
    descriptionRules: [
      value => !!value || 'Informe o nome do servio',
      value => value.length <= 50 || 'O nome do servio deve ter no mximo 50 caracteres'
    ],
    serviceTypeRules: [value => !!value || 'Informe o tipo de servio'],
    durationTimeRules: [value => !!value || 'Informe o tempo de durao'],
    service: null
  }),
  computed: {
    btnDisabled () {
      if (!this.name || this.name.length > 50) return true
      else if (!this.duration || !this.serviceType) return true
      return false
    }
  },
  methods: {
    sendService () {
      window.console.log('antes: ' + this.price)
      const price = this.removeMoneyMask(this.price)
      window.console.log('depois: ' + price) 
      const service = {
        barbershop_id: this.getBarbershop().id,
        name: this.name,
        duration: this.formatResponseToTime(this.duration),
        service_type_id: this.serviceType,
        price: price,
        description: this.description,
        active: this.active
      } 
      this.$emit('sendService', service)
    },
    setValue (value, id) {
      this.$data[id] = value
    },
    showDialogForm (service = null) {
      if (service) {
        this.service = service
        this.setValue(this.service.name, 'name')
        this.setValue(this.service.price, 'price')
        this.setValue(this.service.duration, 'duration')
        this.setValue(this.service.service_type_id, 'serviceType')
        this.setValue(this.service.description, 'description')
        this.setValue(this.service.active, 'active')
      } else {
        this.service.name = null
        this.service.price = null
        this.service.duration= null
        this.service.service_type_id = null
        this.service.description = null
        this.service.active = null
      }
      this.showDialog()
    },
  }
}
</script>